.movie-preview {
  &__body-page {
    padding-top: 1em;
    padding-bottom: 1em;
  }

  &__character {
    .MuiCard-root {
      margin: 0 0.5em 0 0.5em;
    }
  }

  &__trailer-iframe {
    width: 100%;
    height: 720px;
  }

  &__reviews {
    &-text {
      .MuiListItemText-root {
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
        display: -webkit-box;
      }
    }

    &-text--expanded {
      .MuiListItemText-root {
        -webkit-line-clamp: none;
      }
    }
  }
}

@media only screen and (max-width: 425px) {
  .movie-preview {
    &__body-page {
      padding-top: 1em;
      padding-bottom: 1em;
    }

    &__character {
      .MuiCard-root {
        margin: 0 0.5em 0 0.5em;
      }
    }

    &__slider-dots {
      display: none;
    }

    &__trailer-iframe {
      width: 100%;
      height: 240px;
    }
  }
}
